@import '../../styles/variables.scss';
.header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 30px;
  padding-top: 20px;
}
.wraper {
  display: flex;
  align-items: center;
}
.change_theme,
.logo {
  margin-right: 20px;
  cursor: pointer;
}
.logo {
  margin-right: 20px;
  svg {
    width: 65px;
    height: 65px;
  }
}
.title {
  color: $primary-color;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 25px;
}
